import axios from "axios";
import React, { Fragment, useEffect, useState } from "react";
import { MdHighlightOff } from "react-icons/md";
import { TiInputChecked } from "react-icons/ti";
import { connect } from "react-redux";
import { assignRoleToUserAccess } from "../../actions/users";
import Loading from "../../components/Loading/Loading";
import Table from "../../components/Table/Table";
import {
  AssignRoleToUserActionInterface,
  DistrictInterface,
  LocationRoomTableInterface,
  RoomLocationInterface,
  SchoolsInterface,
  SystemUserRoleInterface,
  UserInterface,
} from "../../shared/interfaces";
import { API } from "../../utils/api";
import { errorToText } from "../../utils/errors";
import { LocationsInArray } from "./UserList";

interface AppProps {
  userInfo: UserInterface;
  roleList: SystemUserRoleInterface[];
  district: DistrictInterface[];
  setFeedBack: React.Dispatch<
    React.SetStateAction<{
      type: "success" | "danger" | "warning" | "";
      message: string;
    }>
  >;
  feedBack: {
    type: "success" | "danger" | "warning" | "";
    message: string;
  };
  assignRoleToUserAccess: Function;
  schoolList: SchoolsInterface[] | null;
  roomList: RoomLocationInterface[] | null;
  setChangeRole: React.Dispatch<React.SetStateAction<boolean>>;
}

const UserRole: React.FC<AppProps> = ({
  userInfo,
  roleList,
  district,
  setFeedBack,
  feedBack,
  assignRoleToUserAccess,
  schoolList,
  roomList,
  setChangeRole,
}) => {
  const [loading, setLoading] = useState<boolean>(true);
  const [loadingForm, setLoadingForm] = useState<boolean>(true);
  const [loadindRequest, setLoadindRequest] = useState<boolean>(false);
  const [roleId, setRoleId] = useState<string>("");
  const [provinceId, setProvinceId] = useState<string>("");
  const [districtId, setDistrictId] = useState<string>("");
  const [schoolId, setSchoolId] = useState<string>("");
  const [roomId, setRoomlId] = useState<string>("");
  const [centerId, setCenterId] = useState<string>("");
  const [selectedRole, setSelectedRole] =
    useState<SystemUserRoleInterface | null>(null);

  useEffect(() => {
    if (roleList.length && userInfo) {
      if (userInfo.default_access && loadingForm) {
        if (!roleId.length) {
          setRoleId(userInfo.default_access.system_user_role_id);
          let result = roleList.find(
            (role) => role.system_user_role_id === roleId
          );
          if (result) {
            setSelectedRole(result);
          }
        }
        let LOAD_province: LocationRoomTableInterface | undefined;
        if (userInfo.default_access.district_id && roomList) {
          LOAD_province = LocationsInArray(roomList).find(
            (item) => item.district_id === userInfo.default_access!.district_id
          );
          LOAD_province && setProvinceId(LOAD_province.province_id);
          LOAD_province && setDistrictId(LOAD_province.district_id);
          setLoadingForm(false);
        } else if (userInfo.default_access.room_id && roomList) {
          LOAD_province = LocationsInArray(roomList).find(
            (item) => item.room_id === userInfo.default_access!.room_id
          );
          LOAD_province && setProvinceId(LOAD_province.province_id);
          LOAD_province && setDistrictId(LOAD_province.district_id);
          LOAD_province && setCenterId(LOAD_province.center_id);
          setRoomlId(userInfo.default_access.room_id);
          setLoadingForm(false);
        } else if (
          userInfo.default_access.school_id &&
          roomList &&
          schoolList
        ) {
          let LOAD_school: SchoolsInterface | undefined = schoolList.find(
            (item) => item._id === userInfo.default_access!.school_id
          );
          if (LOAD_school) {
            LOAD_province = LocationsInArray(roomList).find(
              (item) => item.district_id === LOAD_school?.district_id
            );
            LOAD_province && setProvinceId(LOAD_province.province_id);
            LOAD_province && setDistrictId(LOAD_province.district_id);
            setSchoolId(userInfo.default_access.school_id);
            setLoadingForm(false);
          }
        } else {
          setLoadingForm(false);
        }
      }

      setLoading(false);
    }
  }, [
    loadingForm,
    roleId,
    roleList,
    roomId.length,
    roomList,
    schoolList,
    userInfo,
  ]);

  const handleSelectRole = (
    role_id: string,
    TYPE: "SELECT" | "DEFAULT" = "SELECT"
  ) => {
    if (roleList) {
      let result = roleList.find(
        (role) => role.system_user_role_id === role_id
      );
      if (result) {
        setRoleId(role_id);
        setSelectedRole(result);

        if (TYPE === "SELECT") {
          setProvinceId("");
          setDistrictId("");
          setSchoolId("");
          setCenterId("");
          setRoomlId("");
        }
      }
    }
  };

  // useEffect(() => {
  //   if (roleList) {
  //     let result = roleList.find((role) => role.system_user_role_id === roleId);
  //     if (result) {
  //       setSelectedRole(result);
  //     }
  //   }
  // }, [roleList, roleId]);

  // useEffect(() => {
  //   if (selectedRole && selectedRole.school_required) {
  //     console.log("school");
  //     loadSchools();
  //   }
  // },[]);

  // useEffect(() => {
  //   if (selectedRole && selectedRole.room_required) {
  //     console.log("roomsss");
  //     loadRooms();
  //   }
  // });

  const tableData = (): JSX.Element[] => {
    if (roleId.length > 0 && selectedRole === null && roleList) {
      handleSelectRole(roleId, "DEFAULT");
    }
    if (selectedRole) {
      return selectedRole.access.map((access, i) => (
        <tr
          key={i + access.id}
          className={`text-left border-b border-gray-200 hover:bg-gray-100`}
        >
          <td className="px-2 py-1">
            <label className="inline-flex items-center">
              <span className="text-gray-900 capitalize text-xl font-normal cursor-pointer">
                {access.task}
              </span>
            </label>
          </td>
          <td>
            <div
              className={`inline-flex items-center text-xl ${
                access.permitted.create ? "text-green-700" : "text-red-700"
              }`}
            >
              {access.permitted.create ? (
                <TiInputChecked />
              ) : (
                <MdHighlightOff />
              )}
              {access.permitted.create ? " Yes" : "No"}
            </div>
          </td>
          <td>
            <div
              className={`inline-flex items-center text-xl ${
                access.permitted.view ? "text-green-700" : "text-red-700"
              }`}
            >
              {access.permitted.view ? <TiInputChecked /> : <MdHighlightOff />}
              {access.permitted.view ? " Yes" : "No"}
            </div>
          </td>
          <td>
            <div
              className={`inline-flex items-center text-xl ${
                access.permitted.update ? "text-green-700" : "text-red-700"
              }`}
            >
              {access.permitted.update ? (
                <TiInputChecked />
              ) : (
                <MdHighlightOff />
              )}
              {access.permitted.update ? " Yes" : "No"}
            </div>
          </td>
          <td>
            <div
              className={`inline-flex items-center text-xl ${
                access.permitted.delete ? "text-green-700" : "text-red-700"
              }`}
            >
              {access.permitted.delete ? (
                <TiInputChecked />
              ) : (
                <MdHighlightOff />
              )}
              {access.permitted.delete ? " Yes" : "No"}
            </div>
          </td>
        </tr>
      ));
    }
    const optionB = [
      <tr key="55re">
        <td colSpan={5} className="text-center px-2 py-2">
          No Default Access Available on this Role.
        </td>
      </tr>,
    ];
    return optionB;
  };

  const assignRoleToUser = async () => {
    if (!selectedRole) {
      return false;
    }
    if (!window.confirm(`Are sure you want to change user role?`)) {
      return false;
    }
    setLoadindRequest(true);
    let formData: AssignRoleToUserActionInterface = {
      user_id: userInfo.user_id,
      data: {
        system_user_role_id: roleId,
        status: 1,
        role: selectedRole.role,
        access: selectedRole.access,
        school_id: selectedRole.school_required ? schoolId : "",
        district_id: selectedRole.district_required ? districtId : "",
        room_id: selectedRole.room_required ? roomId : "",
      },
    };

    try {
      const res = await axios.post(`${API.AUTH}/addroletouser`, {
        user_id: userInfo.user_id,
        system_user_role_id: roleId,
        status: 1,
        role: selectedRole.role,
        access: true,
        school_id: selectedRole.school_required ? schoolId : "",
        district_id: selectedRole.district_required ? districtId : "",
        room_id: selectedRole.room_required ? roomId : "",
      });
      if (res) {
        assignRoleToUserAccess(formData);
        setLoadindRequest(false);
        setChangeRole(false);
        setFeedBack({
          type: "success",
          message: `Assigning Role to User completed successfully`,
        });
      }
    } catch (error) {
      console.log({ role_error: error });
      setFeedBack({ type: "danger", message: errorToText(error) });
    }
  };

  if (loading === true || userInfo === null) {
    return (
      <div className="text-center mt-7 font-bold text-4xl text-gray-400 animate-pulse">
        Loading...
      </div>
    );
  }
  return (
    <Fragment>
      <div className="grid grid-cols-3 gap-8 my-4">
        {/* left panel */}
        <div className="col-span-1 bg-white w-full shadow-lg p-2">
          <div className="px-12 py-5 bg-white mx-auto rounded-2xl w-100">
            <h3 className="font-semibold text-2xl text-gray-800 mb-4">
              Change User Role
            </h3>
            {loadingForm ? (
              <Loading />
            ) : (
              <div className="space-y-5">
                <div className="space-y-2">
                  <label className="text-sm font-medium text-gray-700 tracking-wide">
                    User Role
                  </label>
                  <select
                    className="block w-full text-base bg-gray-50 text-grey-darker border border-gray-300 px-4 py-3 md:w-full focus:outline-none"
                    required
                    value={roleId}
                    onChange={(e) => {
                      handleSelectRole(e.target.value);
                    }}
                    disabled={loadindRequest}
                  >
                    <option value="">Choose Role</option>
                    {roleList.map((role) => (
                      <option
                        key={role.system_user_role_id}
                        value={role.system_user_role_id}
                      >
                        {role.role}
                      </option>
                    ))}
                  </select>
                </div>
                {selectedRole && selectedRole.room_required && (
                  <div className="space-y-2">
                    <label className="mb-5 text-sm font-medium text-gray-700 tracking-wide">
                      Province
                    </label>
                    <select
                      className="block w-full text-base bg-gray-50 text-grey-darker border border-gray-300 px-4 py-3 md:w-full focus:outline-none"
                      required
                      onChange={(e) => {
                        setProvinceId(e.target.value);
                        setDistrictId("");
                        setSchoolId("");
                        setCenterId("");
                        setRoomlId("");
                      }}
                      value={provinceId}
                      disabled={loadindRequest}
                    >
                      <option value="">Selected province</option>
                      {roomList &&
                        roomList.map((province) => (
                          <option
                            key={province.province_id}
                            value={province.province_id}
                          >
                            {province.province_name}
                          </option>
                        ))}
                    </select>
                  </div>
                )}
                {selectedRole &&
                  (selectedRole.district_required ||
                    selectedRole.room_required) && (
                    <div className="space-y-2">
                      <label className="mb-5 text-sm font-medium text-gray-700 tracking-wide">
                        District
                      </label>
                      <select
                        className="block w-full text-base bg-gray-50 text-grey-darker border border-gray-300 px-4 py-3 md:w-full focus:outline-none"
                        required
                        onChange={(e) => {
                          setDistrictId(e.target.value);
                          setSchoolId("");
                          setCenterId("");
                          setRoomlId("");
                        }}
                        value={districtId}
                        disabled={loadindRequest}
                      >
                        <option value="">Selected district</option>
                        {!provinceId
                          ? district.map((district) => (
                              <option
                                key={district.district_id}
                                value={district.district_id}
                              >
                                {district.district_name}
                              </option>
                            ))
                          : roomList &&
                            roomList
                              .filter(
                                (province) =>
                                  province.province_id === provinceId
                              )[0]
                              .districts.map((district) => (
                                <option
                                  key={district.district_id}
                                  value={district.district_id}
                                >
                                  {district.district_name}
                                </option>
                              ))}
                      </select>
                    </div>
                  )}
                {selectedRole && selectedRole.school_required && (
                  <div className="space-y-2">
                    <label className="mb-5 text-sm font-medium text-gray-700 tracking-wide">
                      School
                    </label>
                    <select
                      className="block w-full text-base bg-gray-50 text-grey-darker border border-gray-300 px-4 py-3 md:w-full focus:outline-none"
                      required
                      onChange={(e) => {
                        setSchoolId(e.target.value);
                        setCenterId("");
                        setRoomlId("");
                      }}
                      disabled={loadindRequest}
                      value={schoolId}
                    >
                      <option value="">Selected school</option>
                      {schoolList &&
                        schoolList.map((school) => (
                          <option value={school._id} key={school._id}>
                            {school.school_name}
                          </option>
                        ))}
                    </select>
                  </div>
                )}

                {selectedRole && selectedRole.room_required && (
                  <div className="space-y-2">
                    <label className="mb-5 text-sm font-medium text-gray-700 tracking-wide">
                      Center
                    </label>
                    <select
                      className="block w-full text-base bg-gray-50 text-grey-darker border border-gray-300 px-4 py-3 md:w-full focus:outline-none"
                      required
                      onChange={(e) => {
                        setCenterId(e.target.value);
                        setRoomlId("");
                      }}
                      disabled={loadindRequest}
                      value={centerId}
                    >
                      <option value="">Select center:</option>
                      {districtId &&
                        districtId.length > 0 &&
                        roomList &&
                        LocationsInArray(roomList)
                          .filter(
                            (district) => district.district_id === districtId
                          )
                          .map((center, index) => (
                            <option
                              value={center.center_id}
                              key={center.center_id + index}
                            >
                              {center.center_name}
                            </option>
                          ))}
                    </select>
                  </div>
                )}

                {selectedRole && selectedRole.room_required && (
                  <div className="space-y-2">
                    <label className="mb-5 text-sm font-medium text-gray-700 tracking-wide">
                      Room
                    </label>
                    <select
                      className="block w-full text-base bg-gray-50 text-grey-darker border border-gray-300 px-4 py-3 md:w-full focus:outline-none"
                      required
                      onChange={(e) => setRoomlId(e.target.value)}
                      disabled={loadindRequest}
                      value={roomId}
                    >
                      <option value="">Select room</option>
                      {centerId &&
                        roomList &&
                        LocationsInArray(roomList)
                          .filter((center) => center.center_id === centerId)
                          .map((room, index) => (
                            <option
                              value={room.room_id}
                              key={room.room_id + 300}
                            >
                              {room.room_name}
                            </option>
                          ))}
                    </select>
                  </div>
                )}

                <div className="flex justify-between 2xl:justify-start gap-2">
                  <button
                    className="btn bg-gray-500 text-gray-50 focus:outline-none"
                    onClick={() => setChangeRole(false)}
                    disabled={loadindRequest}
                  >
                    Back
                  </button>
                  <button
                    className="btn btn-blue focus:outline-none"
                    onClick={() => assignRoleToUser()}
                    disabled={loadindRequest}
                  >
                    {loadindRequest ? "Loading..." : "Save Change"}
                  </button>
                </div>
              </div>
            )}
          </div>
        </div>
        {/* end left panel */}

        {/* right panel */}
        <div className="col-span-2 bg-white w-full shadow-lg text-center py-4 px-12">
          <div className="my-4">
            <span className="text-gray-600 font-semibold text-2xl">
              {selectedRole?.role}'s Default Access
            </span>
          </div>
          {!loading && (
            <Table
              columns={[
                {
                  columnTitle: "Access Name",
                  alignText: "left",
                  padding: "p-3",
                },
                { columnTitle: "Create", alignText: "left" },
                { columnTitle: "View", alignText: "left" },
                { columnTitle: "Update", alignText: "left" },
                { columnTitle: "Delete", alignText: "left" },
              ]}
              rows={tableData}
              pagination={false}
              search={false}
              searchBG="bg-gray-50"
              thColor="gray"
              tableId="defaultAccess"
            />
          )}
        </div>
        {/* end right panel */}
      </div>
    </Fragment>
  );
};

export default connect(null, { assignRoleToUserAccess })(UserRole);
